export default class WayPointsModel {
    #service = null;
    #wayPoints = [];
    constructor(service) {
        this.#service = service;
        this.#wayPoints = this.#service.wayPoints;
    }
    get wayPoints() {
        return this.#wayPoints;
    }
    getWayPointsBy(index) {
        return this.#wayPoints.slice().splice(0, index);
    }
}
