const sortAscending = (a, b) => a - b;
export default class Randomizer {
    static getInteger(min = 0 /* Default.MIN */, max = 100 /* Default.MAX */) {
        [min, max] = [min, max].sort(sortAscending);
        min = Math.ceil(min);
        max = Math.ceil(max);
        return Math.floor(Math.random() * (max - min + 1) + min);
    }
    static get boolean() {
        const value = this.getInteger(0, 1);
        return Boolean(value);
    }
    static getElement(elements) {
        const index = this.getInteger(0, elements.length - 1);
        return elements[index];
    }
}
