import dayjs from 'dayjs';
import View from '../framework/view/view';
const createOffers = (offer) => `
  <li class="event__offer">
    <span class="event__offer-title">${offer.title}</span>
    &plus;&euro;&nbsp;
    <span class="event__offer-price">${offer.price}</span>
  </li>
`;
const createTemplate = ({ wayPoint, destination, offer }) => `
  <div class="event">
    <time class="event__date" datetime="2019-03-18">${wayPoint.dateFrom.format('MMM DD')}</time>
    <div class="event__type">
      <img class="event__type-icon" width="42" height="42" src="img/icons/${wayPoint.type}.png" alt="Event type icon">
    </div>
    <h3 class="event__title">${wayPoint.type} ${destination.name}</h3>
    <div class="event__schedule">
      <p class="event__time">
        <time class="event__start-time" datetime="2019-03-18T10:30">${wayPoint.dateFrom.format('HH:MM')}</time>
        &mdash;
        <time class="event__end-time" datetime="2019-03-18T11:00">${wayPoint.dateTo.format('HH:MM')}</time>
      </p>
      <p class="event__duration">${dayjs.duration(wayPoint.dateTo.diff(wayPoint.dateFrom)).asMinutes()}M</p>
    </div>
    <p class="event__price">
      &euro;&nbsp;<span class="event__price-value">${offer.offers.reduce((a, b) => a + b.price, 0)}</span>
    </p>
    <h4 class="visually-hidden">Offers:</h4>
    <ul class="event__selected-offers">
      ${offer.offers.map(createOffers).join('')}
    </ul>
    <button class="event__favorite-btn ${wayPoint.isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
      <span class="visually-hidden">Add to favorite</span>
      <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
        <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
      </svg>
    </button>
    <button class="event__rollup-btn" type="button">
      <span class="visually-hidden">Open event</span>
    </button>
  </div>
`;
export default class WayPointView extends View {
    #props;
    #handleEditClick;
    #handleFavoriteClick;
    constructor(props) {
        super();
        this.#props = props;
        this.#handleEditClick = this.#props.onEditClick;
        this.#handleFavoriteClick = this.#props.onFavoriteClick;
        this.#initHandlers();
    }
    #initHandlers() {
        this.element?.querySelector('.event__rollup-btn').addEventListener('click', this.#clickHandler.bind(this));
        this.element?.querySelector('.event__favorite-btn').addEventListener('click', this.#clickFavorite.bind(this));
    }
    get template() {
        return createTemplate(this.#props);
    }
    #clickHandler(evt) {
        evt.preventDefault();
        this.#handleEditClick();
    }
    #clickFavorite(evt) {
        evt.preventDefault();
        this.#handleFavoriteClick();
    }
}
