import Randomizer from '../utils/randomizer';
import { DESCRIPTIONS, MOCK_CITIES } from './const.mock';
const RANDOM_FROM = 1;
const RANDOM_TO = 5;
const mockDescription = () => Array.from({ length: Randomizer.getInteger(RANDOM_FROM, RANDOM_TO) }, () => Randomizer.getElement(DESCRIPTIONS)).join(' ');
const mockPicture = (city) => ({
    src: `https://loremflickr.com/248/152/${city}?random=${Randomizer.getInteger()}`,
    description: mockDescription(),
});
const mockDestination = (city) => ({
    id: crypto.randomUUID(),
    description: mockDescription(),
    name: city,
    pictures: Array.from({ length: Randomizer.getInteger(RANDOM_FROM, RANDOM_TO) }, () => mockPicture(city)),
});
const mockDestinations = () => MOCK_CITIES.map(mockDestination);
export { mockDestinations };
